<script>
import ReationPanel from "@/components/ReationPanel.vue";
export default {
  components: { ReationPanel },
  props: {
    messages: Array,
    reactionSettings: Array,
  },
  emits: ["delete-message", "react"],
};
</script>

<template>
  <div class="row mt-5 mb-3">
    <h3>大家的留言</h3>
  </div>
  <div class="row justify-content-md-center">
    <div class="col-md-8">
      <div class="card mb-3" v-for="message in messages" :key="message.id">
        <div
          class="card-header bg-transparent border-bottom-0 d-flex justify-content-between align-items-center"
        >
          <h5 class="card-title">{{ message.name }}</h5>
          <button
            @click="$emit('delete-message', message)"
            type="button"
            class="btn-close"
            aria-label="Close"
          ></button>
        </div>
        <div class="card-body mt-n5">
          <div id="rating">
            <i
              class="bi bi-star-fill"
              v-for="(_, index) in Math.floor(message.rating)"
              :key="index"
            ></i>
            <i class="bi bi-star-half" v-if="message.rating % 1 !== 0"></i>
            <i
              class="bi bi-star"
              v-for="(_, index) in 5 - Math.ceil(message.rating)"
              :key="index"
            ></i>
          </div>
          <p class="card-text">{{ message.body }}</p>
          <footer class="blockquote-footer">
            <div class="reation-panel-container">
              <!--顯示讚、倒讚等各種反應-->
              <ReationPanel
                v-for="(reactionSetting, index) in reactionSettings"
                :key="index"
                @react="$emit('react', message, reactionSetting.name)"
                :react-name="reactionSetting.name"
                :react-count="
                  (message.reactions &&
                    message.reactions[reactionSetting.name]) ||
                  0
                "
                :reacted="message.reacted"
                :icon="reactionSetting.icon"
              ></ReationPanel>
            </div>
          </footer>
        </div>
      </div>
    </div>
  </div>
</template>

<style scoped>
.bi-star-fill,
.bi-star-half {
  color: gold;
}
.reation-panel-container {
  display: flex;
}
</style>
