<script>
export default {
  props: {
    reactName: String,
    reactCount: Number,
    reacted: String,
    icon: Object,
  },
  emits: ["react"],
  computed: {
    isActive() {
      return this.reacted === this.reactName ? "active" : "inactive";
    },
  },
};
</script>

<template>
  <div class="reaction-panel">
    <i @click="$emit('react', reactName)" :class="icon[isActive]"></i>
    &nbsp;{{ reactCount }}
  </div>
</template>

<style scoped>
.reaction-panel {
  margin-right: 10px;
}
</style>
