<script>
import MessageForm from "@/components/MessageForm.vue";
import MessageDisplay from "@/components/MessageDisplay.vue";

export default {
  components: { MessageDisplay, MessageForm },
  data() {
    return {
      // 紀錄當前資料中最高的id值
      maxId: 0,
      // 管理所有留言的陣列
      messages: [],
      reactionSettings: [],
    };
  },
  methods: {
    // 向資料庫請求留言資料
    async fetchMessages() {
      const data = [
        {
          id: 1,
          name: "陳先生",
          rating: 3,
          body: "東西不錯，可惜環境有點亂",
          reactions: {
            like: 7,
            unlike: 1,
          },
          reacted: "",
        },
        {
          id: 2,
          name: "林小姐",
          rating: 5,
          body: "便宜又好吃，下次會再來",
          reactions: {
            like: 3,
            unlike: 0,
          },
          reacted: "",
        },
        {
          id: 3,
          name: "李同學",
          rating: 0.5,
          body: "生魚片沒熟",
          reactions: {
            like: 1,
            unlike: 5,
          },
          reacted: "",
        },
      ];
      this.maxId = data.length === 0 ? 0 : data[data.length - 1].id;
      this.messages = data;
    },
    // 取得所有和反應有關的設定
    async fetchReactionSettings() {
      const data = [
        {
          name: "like",
          icon: {
            active: "bi bi-hand-thumbs-up-fill",
            inactive: "bi bi-hand-thumbs-up",
          },
        },
        {
          name: "unlike",
          icon: {
            active: "bi bi-hand-thumbs-down-fill",
            inactive: "bi bi-hand-thumbs-down",
          },
        },
      ];
      this.reactionSettings = data;
    },
    // 發送留言，存到資料庫並更新頁面
    async sendMessage(data) {
      if (data.name) {
        data.id = this.maxId + 1;
        // 模擬將表單存到遠端資料庫的行為
        try {
          console.log("表單內容存到資料庫：", data);
          // 如果存儲成功，則將資料添加到 messages
          // deepcopy避免新增在畫面上後card物件還會跟著表單元件跑
          const response = JSON.parse(JSON.stringify(data));
          this.messages.push(response);
          this.maxId++;
        } catch (error) {
          console.error("發生錯誤：", error);
        }
      } else {
        alert("請填寫您的名字。");
      }
    },
    // 刪除留言，留言將從資料庫中刪除並更新頁面
    async deleteMessage(message) {
      if (confirm("確定刪除留言？")) {
        try {
          console.log("刪除留言：", message);
          this.messages = this.messages.filter((msg) => msg !== message);
        } catch (error) {
          console.error("發生錯誤：", error);
        }
      }
    },
    // 按下/取消讚或不喜歡
    async react(message, name) {
      if (!Object.prototype.hasOwnProperty.call(message, "reactions")) {
        message.reactions = {};
      }
      switch (message.reacted || "") {
        case "":
          if (!Object.prototype.hasOwnProperty.call(message.reactions, name)) {
            message.reactions[name] = 1;
          } else {
            message.reactions[name]++;
          }
          message.reacted = name;
          break;
        default:
          if (message.reacted === name) {
            message.reactions[name]--;
            message.reacted = "";
          }
      }
      console.log(message);
    },
  },
  mounted() {
    this.fetchMessages();
    this.fetchReactionSettings();
  },
};
</script>

<template>
  <div class="row mt-5 mb-3">
    <h1>網友評論</h1>
  </div>
  <MessageForm @form-submit="sendMessage"></MessageForm>
  <hr />
  <MessageDisplay
    :messages="messages"
    :reaction-settings="reactionSettings"
    @delete-message="deleteMessage"
    @react="react"
  ></MessageDisplay>
</template>

<style scoped></style>
