<script>
import vue3StarRatings from "vue3-star-ratings";
export default {
  components: {
    vue3StarRatings: vue3StarRatings,
  },
  data() {
    return {
      formData: {
        name: "",
        rating: 0,
        body: "",
      },
    };
  },
  methods: {
    onSubmit() {
      this.$emit("form-submit", this.formData);
    },
  },
  emits: ["form-submit"],
  watch: {
    "formData.rating": function (newVal) {
      // 以0.5為單位四捨五入ranking
      this.formData.rating = Math.round(newVal * 2) / 2;
    },
  },
};
</script>

<template>
  <div class="row justify-content-md-center">
    <div class="card col-md-6 mb-3">
      <div class="card-header bg-transparent border-bottom-0 p-3">
        <h3>請留下您的回饋！</h3>
      </div>
      <div class="card-body">
        <!-- @submit 加上 .prevent 避免頁面刷新 -->
        <form @submit.prevent="onSubmit" class="p-3">
          <div class="row">
            <div class="form-group col mb-3">
              <label for="name-input" class="form-label">留言人</label>
              <input
                v-model="formData.name"
                class="form-control"
                id="name-input"
                name="name"
                type="text"
                placeholder="你的名字"
              />
            </div>
            <div class="form-group col mb-3">
              <label for="ranking-input" class="form-label">評分</label>
              <div id="ranking-input">
                <vue3-star-ratings
                  v-model="formData.rating"
                  inactive-color="#B9B9B9"
                />
              </div>
            </div>
          </div>
          <div class="form-group row mb-3 align-items-center">
            <div class="col">
              <label for="message-input" class="form-label">想說的話</label>
              <textarea
                v-model="formData.body"
                class="form-control ml-5"
                id="message-input"
                name="message"
                rows="3"
              ></textarea>
            </div>
          </div>
          <button type="submit" class="btn btn-primary">Submit</button>
        </form>
      </div>
    </div>
  </div>
</template>

<style scoped>
form {
  text-align: left;
}
</style>
